/* Navbar */

.navbar {
     height: 70px;
}

.navbar-item img {
     max-height: 1000rem;
}

.navbar-brand {
     margin-left: 10vw;
}

.end-padding {
     width: 70px;
     margin-right: 15px;
     height: 70px;
}

.navbar-burger {
     height: 70px;
     width: 70px;
     margin-right: 12vw;
}

.navbar-item {
     padding: 0.5rem 2.25rem;
}

.logo {
     height: 3.25rem;
     color: #d8d8d8;
     font-size: 22px;
     line-height: 1.5;
     margin-top: 17px;
     /* margin-right: 15vw; */
}

a {
     color: #d8d8d8;
}

a:hover {
     color: #d8d8d8;
}

.navbarEndColor {
     background-color: #0a0a0a;
}

#navbarBasicExample.navbarCustomActive {
     background-color: #0a0a0a;
     padding: 0;
}

a.navbar-item:hover {
     background-color: #000000;
     color: #1c1e20;
}

.spacer {
     margin-right: 10vw;
}

.pageContainer {
     min-height: 100vh;
}

.innerColumn {
     background-color: #4f4f4f;
     padding-right: 5vw;
     padding-left: 5vw;
     margin-right: 10vw;
     margin-left: 10vw;
     padding-top: 4vh;
     min-height: 100vh;
     box-shadow: 0 0 3px rgb(24, 24, 24);
     padding-bottom: 5vh;
}

.blockWrapper
 {
     background-color: #363636;
     padding: 15px;
     border-radius: 15px;
     box-shadow: 0 0 3px #333333;
}

.homeWrapper {
     list-style-type: disc;
}

.homeHeader {
     font-size: 17px;
}

.homeSpace {
     margin-top: 35px;
}

.homeItem {
     color: rgb(182, 182, 182);
     margin-left: 20px;
}

/* NoMatch Styles */

.NoMatchContainer {
     text-align: center;
}

.NoMatchHeader {
     font-size: 34px;
}

.duckerz {
     margin-top: 15px;
     display: block;
     margin-left: auto;
     margin-right: auto;
}

.blockWrapper {
     margin-bottom: 30px;
     background-color: #363636;
     padding: 15px;
     border-radius: 15px;
     box-shadow: 0 0 3px #333333;
}

.blockTitle {
     font-size: 20px;
     margin-bottom: 15px;
}

.toggleButton {
     float: right;
     margin-top: -5px;
     padding: 10px 15px 10px 15px;
     border-radius: 10px;
     box-shadow: 0 0 3px #000000;
}

.toggleButton:hover,
.visVisbile,
.visHidden {
     -webkit-transition-duration: 0.2s;
             transition-duration: 0.2s;
}

.visVisbile {
     display: block;
}

.visHidden {
     display: none;
}

.headerTitle {
     font-size: 22px;
     background-color: #363636;
     color: #e6e6e6;
     box-shadow: 0 0 3px #333333;
     padding: 15px;
     border-radius: 15px;
     margin-bottom: 25px;
     display: inline-block;
}


.loadingContainer {
    text-align: center;
    margin-top: 15vh;
}
.featuredContainer {
     cursor: pointer;
}

.fContainerWrapper {
     margin-bottom: 5px;
     margin-top: 5px
}

.visState {
     display: list-item;
     color: whitesmoke;
     margin-bottom: 5px;
     list-style: circle;
}

.hidState {
     display: none;
}

.mleft {
     margin-left: 40px;
}

.postText {
     color: rgb(182, 182, 182);
}

.listWrapper {
     margin-bottom: 30px;
     background-color: #363636;
     padding: 10px 15px 15px 15px;
     border-radius: 15px;
     box-shadow: 0 0 3px #333333;
}

.video-container {
     position: relative;
     padding-bottom: 56.25%;
     height: 0;
     overflow: hidden;
     margin-bottom: 35px;
     background-color: rgb(61, 61, 61);
     box-shadow: 0 0 7px rgb(24, 24, 24);
}

.video-container iframe,
.video-container object,
.video-container embed {
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
}

.featuredWrapper {
     margin-top: 22px;
     background-color: #363636;
     font-size: 17px;
     padding: 10px;
     border-radius: 10px;
     color: white;
     box-shadow: 0 0 3px black;
}

.tagsWrapper {
     padding: 10px;
     background-color: #363636;
     font-size: 17px;
     border-radius: 10px;
     color: black;
     box-shadow: 0 0 3px black;
}

.loadingContainer {
     text-align: center;
     margin-top: 15vh;
}

.titleText {
     color: whitesmoke;
}

.postText {
     color: rgb(182, 182, 182);
}

.featuredItem {
     display: list-item;
     color: rgb(182, 182, 182);
     margin-bottom: 5px;
     list-style: circle;
     margin-left: 40px;
}

.tagItem {
     display: list-item;
     color: rgb(182, 182, 182);
     margin-bottom: 5px;
     list-style: circle;
     margin-left: 40px;
}

.pressItemWrapper {
     list-style-type: disc;
     font-size: 14px;
     margin: 10px 0px 10px 10px;
}

.pressItem {
     color: rgb(182, 182, 182);
}

.pressItem:hover {
     color: rgb(182, 182, 182);
     text-decoration: underline;
}

.tag:not(body).is-dark {
     word-wrap: normal;
     background-color: #363636;
     color: whitesmoke;
     font-size: 17px;
     box-shadow: 0 0 3px #333333;
}

* {
     margin: 0;
     padding: 0;
}

@font-face {
     font-family: "Alata";
     src: url(/static/media/Alata-Regular.03a7f67c.ttf) format("truetype");
}

body {
     font-family: "Alata";
     background-color: #2e2e2e;
     color: #e6e6e6;
     overflow-x: hidden;
     overflow-y: hidden;
     max-width: 100%;
}

